<template>
    <div class="index_banner">
        <div class="swiper-container" :style="{ height: bannerHeight + 'px' }">
            <div class="swiper-wrapper">
                <div class="swiper-slide" v-for="item in list" :key="item.id" :style="{'background-image': 'url(' + imgFormatter(item.cover) + ')'}">
                    
                    <a href="javascript:;" @click="openProduct(item.url)" v-if="item.url && item.url.indexOf('open:') > -1"></a>
                    
                    <router-link :to="handlerUrl(item.url)" v-else-if="item.url && item.url.indexOf('http') == -1"></router-link>
                    
                    <a :href="item.url" v-else-if="item.url && item.url.indexOf('http') > -1" target="_blank"></a>
                </div>
            </div>
            <!-- Add Pagination -->
            <div class="swiper-pagination"></div>
        </div>
        <div class="announce_list" v-if="announceList.length">
            <span class="icon">
                <i class="fas fa-volume-up"></i>
            </span>
            <vueSeamless :data="announceList" :class-option="{ direction: 2, limitMoveNum: 1 }" class="seamless-warp">
                <ul class="item" :style="{ width: announceListWidth }">
                    <li v-for="item in announceList" v-text="item.announce_content" :key="item.id"></li>
                </ul>
            </vueSeamless>
        </div>

        <el-dialog
            title="通知"
            :visible.sync="alertDialogShow"
            width="90%"
            class="alert_dialog"
        >
            <div style="padding: 0 10px;">
                <!--<p :class="{ h: /[1-9]./.test(item) }" v-for="(item, index) in alertContent" :key="index"> {{ item }} </p>-->
                <div class="alert_item" v-for="(item, index) in alertContent" :key="index">
                    <div class="h" v-if="alertContent.length > 1">公告{{ numberToChinese(index) }}：</div>
                    <p v-for="(item_content, _index) in item" :key="_index"> {{ item_content }} </p>
                </div>
            </div>

            <span slot="footer" class="dialog-footer">
                <el-button @click="alertDialogShow = false">知道了</el-button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
import request from '@/utils/request'
import { imgFormatter, numberToChinese } from '@/utils/index'
import { getBannerList } from '@/api/common'
import vueSeamless from 'vue-seamless-scroll'

import { 
    getBannerAndAnnounceList
} from '@/api/common'

export default {
    name: 'Home-Banner',
    props: ['productClick'],

    components: {
        vueSeamless
    },

    data() {
        return {
            list: [],
            announceListWidth: 0,
            announceList: [
                
            ],
            alertDialogShow: false,
            alertContent: '',
        }
    },
    computed: {
		user() { return this.$store.getters.user },
		app() { return this.$store.getters.app },
        product() { return this.$store.getters.product },
    },

    mounted() {
        this.init();
    },

    methods: {
       init() {
           if(this.list.length) return ;

           getBannerAndAnnounceList().then(json => {
               if (json.banner.code == 200) {
                    this.list = json.banner.list.data;
                    
                    // 自适应高度
                    const width = (window.innerWidth ? window.innerWidth : document.documentElement.clientWidth ? document.documentElement.clientWidth : screen.width);
                    this.bannerHeight = width * 1000/1920;
                    this.$nextTick(() => {
                        this.initSwiper();
                    })
				} else {
                    // this.$message.error(json.info);
                }
                
                if(json.announce.code == 200) {
                    const list = json.announce.list.data.filter(item => item.type == 'home');

                    let length = 0;
                    list.forEach(a => {
                        length += a.announce_content.replace(/\n|\r|\s/g, '').length;
                    })

                    this.announceListWidth = (length * 14 + 10 * list.length) + 'px';
                    this.announceList = list;

                    const alertList = json.announce.list.data.filter(item => item.type == 'alert');

                    if(alertList.length && !window.alertInit) {
                        window.alertInit = 1;
                        // this.alertContent = alertList[0].announce_content.split(/\n/);
                        this.alertContent = alertList.map(item => item.announce_content.split(/\n/));
                        this.alertDialogShow = true;
                    }

                } else {
                    // this.$message.error(json.info);
                }
            });
       },

       initSwiper() {
            new window.Swiper('.index_banner .swiper-container', {
                pagination: {
                    el: '.index_banner .swiper-pagination',
                    dynamicBullets: true,
                },
                autoplay: {
                    delay: 3000,
                    disableOnInteraction: false,
                },
            });
       },

       imgFormatter(src) {
           return imgFormatter(src);
       },
       
       openProduct(url) {
            
        const id = url.replace('open:', '');
        
        let product = this.product.list.filter(p => p.id == id);
        
        // console.log(product);
        
        if(product && product.length) {
            this.productClick && this.productClick(product[0]);
        }
       },

       handlerUrl(url) {
           let obj;

           try {
              obj = JSON.parse(url);
           }catch(e) {

           }

           if(obj && obj.mobile) {
               return obj.mobile;
           }

           return url;
       },
       
       numberToChinese(index) {
           return numberToChinese(index + 1);
       },
    }
}
</script>

