<template>
    <div class="home_index">
        <Banner :productClick="productClick" />
        
        <ProductList v-if="app.siteInfo.yinhe" :productClick="productClick" />
        
        <div class="product_list" v-else>
            <el-row :gutter="10">
                <el-col :span="12">
                    <div class="grid-content bg-purple">
                        <div class="p_item lg" :class="{ hightOdds: !!app.hightOddsId }" :style="{backgroundImage: `url(${navIconList[0].bg})`}" @click="navClick(navIconList[0].id)">
                            <span>{{ navIconList[0].name }}</span>
                        </div>
                        
                        <div class="p_item" :style="{backgroundImage: `url(${navIconList[4].bg})`}" @click="navClick(navIconList[4].id)">
                            <div>
                                <img :src="navIconList[4].icon" height="100%" />
                                <span>{{ navIconList[4].name }}</span>
                            </div>
                        </div>
                    </div>
                    
                </el-col>
                <el-col :span="12">
                    <div class="grid-content bg-purple">
                        <div class="p_item" :style="{backgroundImage: `url(${navIconList[index].bg})`}" :key="index" v-for="index in [1, 2, 3, 5]"  >
                            <div @click="navClick(navIconList[index].id)">
                                <img :src="navIconList[index].icon" height="100%" />
                                <span>{{ navIconList[index].name }}</span>
                            </div>
                        </div>
                    </div>
                </el-col>
            </el-row>

            <el-row :gutter="10" class="sub_btn">
                <!--el-col :span="8">
                    <div class="grid-content bg-purple">
                        <el-button @click="$router.push('/charge')" icon="fas fa-yen-sign">存款</el-button>
                    </div>
                </el-col-->
                <!-- <el-col :span="8">
                    <div class="grid-content bg-purple">
                        <el-button @click="$router.push('/user/exchange')" icon="fas fa-exchange-alt">转账</el-button>
                    </div>
                </el-col> -->
                <el-col :span="12">
                    <div class="grid-content bg-purple">
                        <el-button  class="hot" @click="freeVideoClick" icon="fas fa-fire-alt">
                            免费影城
                        </el-button>

                    </div>
                </el-col>
                <el-col :span="12">
                    <div class="grid-content bg-purple">
                        <el-button class="purple" @click="$router.push('/help')" icon="fas fa-comment-dots">客服</el-button>
                    </div>
                </el-col>
            </el-row>
        </div>

        <el-dialog
            class="product_dialog"
            :visible.sync="productDialogVisible"
            :width="productDialogWidth"
            top="0px"
        >
            <div class="product_list" ref="product_list">
                <ul ref="product_ul">
                    <li v-for="item in selectProductList" :key="item.id" @click="productClick(item)">
                        <a class="product_item" href="javascript:;">
                            <img v-if="productImgLoaded[item.id]" :src="getProductImg(item.id)" />

                            <!-- <el-image :src="getProductImg(item.id) + ''"></el-image> -->
                            <div class="text" v-else :style="{ height: productDialogImgHeight ? (productDialogImgHeight + 'px') : 'auto', lineHeight: productDialogImgHeight ? (productDialogImgHeight + 'px') : 'auto' }">
                                <i class="el-icon-loading" v-if="getProductImg(item.id)"></i>
                                {{ item.name }}
                            </div>

                            <div class="fix" v-if="item.is_fix == 'Y'">
                                <span>维护中</span>
                            </div>
                        </a>
                    </li>
                </ul>
            </div>
        </el-dialog>
    </div>
</template>

<script>
import { mapGetters } from 'vuex'
import event from '@/utils/event'
import Banner from './Banner'
import ProductList from './ProductList';
import BScroll from 'better-scroll'
import { getUrl, transferallout } from '@/api/product'
import { transfer  } from '@/api/user'

import { imgFormatter, encrypto } from '@/utils/index'


let winOpen = null;

export default {
    name: 'Home-Index',
    components: {
        Banner,
        ProductList,
    },
    data() {
        const navIconList = [
            {
                // 彩票 bg: imgFormatter('/images/2019/06/27/dd172103-7be1-4f14-94ff-01eab5ec57b4.jpg'),
                // bg: imgFormatter('/images/2019/11/06/e66c7def-4e52-42c9-89d9-a7f977493f08.jpg'),
                bg: imgFormatter('/images/2019/11/06/cd6f6ae5-13b3-4ae0-8995-fcc43a136d3b.jpg'),
                icon: imgFormatter('/images/2019/06/04/0a24a847-be88-435c-8605-6ab7f37258e4.png'),
                // name: '全球彩票',
                // id: 4,
                name: '鑫盛体育',
                // name: 'M8体育',
                id: 7,
            },
            {
                // 体育 bg: imgFormatter('/images/2019/11/06/e66c7def-4e52-42c9-89d9-a7f977493f08.jpg'),
                bg: imgFormatter('/images/2019/06/13/9b99802d-4f11-4a3f-bce9-853fc25f3096.jpg'),
                icon: imgFormatter('/images/2019/06/04/4e94be13-a749-4b48-88a6-73e7d9bdef25.png'),
                // name: '体育竞技',
                // id: 7,
                name: '全球彩票',
                id: 4,
            },
            {
                bg: imgFormatter('/images/2019/06/13/7432e24e-01e2-4233-a65a-5bb26d010ede.jpg'),
                icon: imgFormatter('/images/2019/06/04/ae4a5b24-d881-4444-b098-701949d1365e.png'),
                name: '电子游艺',
                id: 2,
            },
            {
                bg: imgFormatter('/images/2019/06/13/4bacc140-89f0-4a38-8b01-23bd75240a3e.jpg'),
                icon: imgFormatter('/images/2019/06/04/37ae16d5-2aa1-467c-b432-35a14c3abe67.png'),
                name: '棋牌娱乐',
                id: 3,
            },
            {
                bg: imgFormatter('/images/2019/06/13/bdad221a-9b8c-46b0-b189-89ebed95afbd.jpg'),
                icon: imgFormatter('/images/2019/06/04/cc5a3def-be46-4fc6-9aaf-0e042d506a8f.png'),
                name: '真人视讯',
                id: 1,
            },
            {
                bg: imgFormatter('/images/2019/06/13/51a29686-9b74-4f45-a2f3-8657420f0e29.jpg'),
                icon: imgFormatter('/images/2019/06/04/a3bbe7ff-7962-4ded-8f34-978871d458e5.png'),
                name: '电子竞技',
                id: 8,
            },
        ]

        const productPics = [
            { name: "SG彩票", id: 11, img: imgFormatter('/images/2019/11/06/333f000e-be68-41b0-9d83-037221bce659.jpg') },
            { name: "BG彩票", id: 20, img: imgFormatter('/images/2019/11/06/62ab5757-ed96-4458-9033-81a71c141be7.jpg') },
            { name: "环球彩票", id: 28, img: imgFormatter('/images/2019/12/09/e0ea1f01-60cd-42f1-b5de-2f0c132face4.jpg') },
            { name: "BG视讯", id: 21, img: imgFormatter('/images/2019/11/06/730e19e6-8400-4363-a68e-e340569947a7.jpg') },
            { name: "AG视讯", id: 17, img: imgFormatter('/images/2019/11/06/129cc921-2877-497a-83b8-004e296ba791.jpg') },
            { name: "SA视讯", id: 19, img: imgFormatter('/images/2019/11/06/296faf31-dbf5-466d-a8e0-ed35c3561115.jpg') },
            { name: "BB视讯", id: 26, img: imgFormatter('/images/2019/11/06/05822c8e-07f8-4a73-972b-f78e973b742c.jpg') },
            { name: "申博视讯", id: 27, img: imgFormatter('/images/2019/11/06/7f8d1374-26c7-4a19-9c7d-4ad4ecdd70a1.jpg') },
            { name: "VG棋牌", id: 1, img: imgFormatter('/images/2019/11/06/1adc6297-a4df-4ad2-b5b9-e3d427318997.jpg') },
            { name: "天发棋牌", id: 13, img: imgFormatter('/images/2019/11/06/fa275133-919d-468b-9a14-eae079417c8d.jpg') },
            { name: "欢乐棋牌", id: 23, img: imgFormatter('/images/2019/11/06/99cbce4f-f4a0-44ab-b063-857a2511be3e.jpg') },
            { name: "BG捕鱼", id: 22, img: imgFormatter('/images/2019/11/06/12ad9bb2-1656-41a4-851a-bb4dc070b1ff.jpg') },
            { name: "女优电子", id: 10, img: imgFormatter('/images/2019/11/06/9eb2d04c-1e4a-49fa-97de-73f969c5d5a2.jpg') },
            { name: "SA老虎机", id: 18, img: imgFormatter('/images/2019/11/06/c52260dc-0d4c-449a-b3de-11e236624c64.jpg') },
            { name: "IM电竞", id: 12, img: imgFormatter('/images/2019/11/06/fb78d347-926c-4988-a49f-7568fb98daba.jpg') },
            { name: "泛亚电竞", id: 2, img: imgFormatter('/images/2019/11/06/ace7bdbf-4920-4514-9471-4ea4232c9004.jpg') },
            { name: "开元棋牌", id: 30, img: imgFormatter('/images/2020/02/13/fd4f0e55-36d0-4b58-8366-c67bc516766f.jpg') },
            { name: "夺宝电子", id: 31, img: imgFormatter('/images/2020/02/24/1ac79b98-9521-4b61-86f7-156be139ec85.jpg') },
        ]

        return {
            navIconList,
            productPics,
            productDialogVisible: false,
            selectedNavId: null,
            loading: false,

            productDialogWidth: '80%',
            productDialogImgHeight: '',
            productImgLoaded: {

            },
        }
    },

    computed: {
		user() { return this.$store.getters.user },
		app() { return this.$store.getters.app },
        product() { return this.$store.getters.product },
        selectProductList() {
            if(!this.selectedNavId) return [];

            const list = this.product.list.filter(p => {
                // return true;
                return p.nav_id == this.selectedNavId && p.is_show == 'Y';
            });

            if(process.env.NODE_ENV != 'development') {

                // if(this.app.isApp && this.app.isIOS) {
                //     list.forEach(p => {
                //         if(p.id == 6) {
                //             p.is_fix = 'Y';
                //         }
                //     })
                // }
            }

            return list;
        },

        filterProduct() {
            const checkNavIsShow = (id) => {
                let res = false;
                this.product.navList.forEach(nav => {
                    nav.list && nav.list.forEach(p => {
                        if(p.id == id) {
                            if(nav.is_show == 'Y') {
                                res = true;
                            } 
                        }
                    })
                })

                return res;
            }

            const list = this.product.list.filter(item => {
                return item.is_show == 'Y' && item.is_fix == 'N' && checkNavIsShow(item.id);
            });

            return list;
        },

        bodyRect() {
            const rect = document.body.getBoundingClientRect()
            return rect;
        },
    },

    mounted() {
        this.init();
        
    },

    methods: {
        init() {
            // 邀请码
            const code = this.$route.query.c;

            if(code) {
                window.localStorage.setItem('code', code);
                this.$router.push({ path: this.user.roles.indexOf('user') == -1 ? '/auth/register' : '/' });
            }

            // 电影网邀请码
            const sign = this.$route.query.sign;
            if(sign && sign != window.localStorage.getItem('sign')) {
                window.localStorage.setItem('sign', sign);
            }
            
            const username = this.$route.query.username;
            if(username && username != window.localStorage.getItem('username')) {
                window.localStorage.setItem('username', username);
            }

            event.$emit('changeTab', 1);

            // 判断是否是app
            const app = this.$route.query.app;

            if(app == 'ios') {
                window.localStorage.setItem('app', app);
            }else if(app == 'android') {
                window.localStorage.setItem('app', app);
            }
        },

        navClick(navId) {
            // this.$router.push('/nav/' + navId);
            this.selectedNavId = navId;

            if(navId == 7) {
                const product = this.selectProductList[0];
                this.productClick(product);
                return ;
            }

            this.productDialogVisible = true;
            

            this.$nextTick(() => {
                const length = this.selectProductList.length;
                let productDialogImgHeight = (2 + 175/417 * (this.bodyRect.width * 0.8 - 4));
                const height = length * productDialogImgHeight;

                if(height > this.bodyRect.height * .8) {
                    this.productDialogWidth = (0.8 * (this.bodyRect.height * .8 / height)) * 100 + '%';
                    this.productDialogImgHeight = this.bodyRect.height * .8 / length;
                }else {
                    this.productDialogWidth = '65%';
                    this.productDialogImgHeight = productDialogImgHeight;
                }
            });

            this.selectProductList.forEach(item => {
                if(!this.productImgLoaded[item.id]) {
                    const src = this.getProductImg(item.id);
                    if(src) {
                        const img = new window.Image();
                        img.onload = () => {
                            this.$set(this.productImgLoaded, item.id, true);
                        };

                        img.src = src;
                    }
                }
            });
        },

        getProductImg(id) {
            let url = '';

            this.productPics.forEach(item => {
                if(item.id == id) {
                    url = item.img;
                }
            })

            return url;
        },

        productClick(product) {
            
            if(this.user.roles.indexOf('user') == -1 && this.user.roles.indexOf('try') == -1) {
                this.$message.error('请先登录账号');
                return ;
            }
            

           if(product.is_fix == 'Y') {
               this.$message.error('接口维护中');
               return ;
           }

           if(this.app.isApp) {
               this.appItemClick(product);
           }else {
                const id = product.id;

                if(id == 10 || id == 18) {
                    this.$router.push('/product/' + id);
                }else {
                    if(winOpen) {
                        winOpen.close();
                    }

                    winOpen = window.open("", "product");

                    setTimeout(() => {
                        const link = this.$router.resolve('/redirect?pid=' + id);
                        
                        if(this.app.isDev) {
                            console.log(window.location.host + link.href);
                        }
                        
                        winOpen.location = link.href;
                    }, 100);
                }
           }
       },
       
       appItemClick(product) {
            if(product.id == 10 || product.id == 18) {
                const url = '/product/' + product.id;
                this.$store.dispatch('postMsgToApp', {
                    action: 'presentModal',
                    data: {
                        url,
                        title: product.name
                    }
                })
            }else if(product.id == 6) {
                const url = '/redirect?pid=' + product.id;
                this.$store.dispatch('postMsgToApp', {
                    action: 'presentModal',
                    data: {
                        url,
                        title: product.name
                    }
                })
            }else {
                // if(this.loading) return ;
                // this.loading = true;
                this.loading = this.$loading({
                    lock: true,
                    text: this.app.autoExchange ? '余额自动转入游戏中' : '加载中',
                    spinner: 'el-icon-loading',
                    background: 'rgba(0, 0, 0, 0.7)'
                });

                if(this.app.autoExchange) {
                    this.reloadBalance(product, (hadExchange) => {
                        if(hadExchange) {
                            const product_ids = this.filterProduct.map(item => { return item.id }).join(',');
                            this.$store.dispatch('productBalanceByAjax', { product_ids }).then(() => {
                                this.getProductUrlInApp(product);
                            });
                        }else {
                            this.getProductUrlInApp(product);
                        }
                    });
                }else {
                    this.getProductUrlInApp(product);
                }
            }
        },

        reloadBalance(product, callback) {
            const product_ids = this.filterProduct.map(item => { return item.id }).join(',');
            // this.loading = true;
            this.$store.dispatch('productBalanceByAjax', { product_ids }).then((json) => {
                if(json.code == 200) {
                    let idArr = [], total_money = 0;
                    for(let id in json.list) {
                        const money = Number(json.list[id].money);
                        
                        if(id != 0 && id != product.id) {
                            // 大于 0 则 回收
                            if(money > 0) {
                                idArr.push(id);
                            }
                        }

                        if(id == 0) {
                            total_money = money
                        }
                    }

                    if(idArr.length) {
                        const data = {
                            type: 3,
                            product_id: idArr.join(','),
                        }

                        transfer(data).then(json => {
                            if (json.code == 200) {
                                transferallout(product.id).then(() => {
                                    setTimeout(() => {
                                        this.loading.close();
                                        callback && callback(1);
                                    }, 3000);     
                                });
                            } else {
                                this.$message.error(json.info);
                                this.loading.close();
                                callback && callback();
                            }
                        });
                    }else {
                        if(total_money > 0) {
                            transferallout(product.id).then(() => {
                                this.loading.close();
                                callback && callback();
                            })
                        }else {
                            this.loading.close();
                            callback && callback();
                        }
                    }

                }else {
                    this.$message.error(json.info);
                    this.loading.close();
                    callback && callback();
                }
            });
        },

        getProductUrlInApp(product) {
            const data = {
                product_id: product.id,
                gameversion: 2,
                orientation: 2,
            }

            this.loading = true;
            getUrl(data).then(json => {
				if (json.code == 200) {
                    const url = json.list.login_url;

                    if(product.id == 17) {
                        this.$store.dispatch('postMsgToApp', {
                            action: 'openURL',
                            data: {
                                url: url,
                            }
                        })
                    }else if(product.id == 20 && this.app.isIOS) {
                        this.$store.dispatch('postMsgToApp', {
                            action: 'openURL',
                            data: {
                                url: url,
                            }
                        })
                    }
                    else {

                        this.$store.dispatch('postMsgToApp', {
                            action: 'presentModal',
                            data: {
                                url,
                                title: product.name
                            }
                        })

                    }

				} else {
					this.$message.error(json.info);
                }
                
                setTimeout(() => {
                    this.loading = false;
                }, 500);
			});
        },

        freeVideoClick() {
            const isApp = this.app.isApp;

            if(isApp) {
                const fn = (n) => { return n<10?'0'+n:n; }
                let d = new Date();
                d = "" + d.getFullYear() + fn(d.getMonth() + 1) + fn(d.getDate());
                const key = encrypto('A45yUg9rlZ4L' + d, 134, 23);
                const url = 'http://91qz.top?sykey=' +key;
                
                this.$store.dispatch('postMsgToApp', {
                    action: 'presentModal',
                    data: {
                        url,
                        title: '免费影城'
                    }
                })

            }else {
                this.$alert('该福利仅限于APP使用，请下载APP', '提示', {
                    confirmButtonText: '确定',
                });
            }

            // this.$router.push('/user/exchange');
        }

    }
}
</script>

