    computed: {
		user() { return this.$store.getters.user },
		app() { return this.$store.getters.app },
        product() { return this.$store.getters.product },
        selectProductList() {
            if(!this.selectedNavId) return [];

            const list = this.product.list.filter(p => {
                // return true;
                return p.nav_id == this.selectedNavId && p.is_show == 'Y';
            });

            if(process.env.NODE_ENV != 'development') {

                // if(this.app.isApp && this.app.isIOS) {
                //     list.forEach(p => {
                //         if(p.id == 6) {
                //             p.is_fix = 'Y';
                //         }
                //     })
                // }
            }

            return list;
        },

        filterProduct() {
            const checkNavIsShow = (id) => {
                let res = false;
                this.product.navList.forEach(nav => {
                    nav.list && nav.list.forEach(p => {
                        if(p.id == id) {
                            if(nav.is_show == 'Y') {
                                res = true;
                            } 
                        }
                    })
                })

                return res;
            }

            const list = this.product.list.filter(item => {
                return item.is_show == 'Y' && item.is_fix == 'N' && checkNavIsShow(item.id);
            });

            return list;
        },
    },

    mounted() {
        this.init();
    },

    methods: {
       init() {
           this.initSwiper();
       },

       initSwiper() {
            var galleryThumbs = new Swiper('.home_product_list .gallery-thumbs', {
            //   spaceBetween: 10,
              slidesPerView: 5,
              freeMode: true,
              watchSlidesVisibility: true,
              watchSlidesProgress: true,
            });
            
            var galleryTop = new Swiper('.home_product_list .gallery-top', {
            //   spaceBetween: 10,
              thumbs: {
                swiper: galleryThumbs
              }
            });
       },
       
       filterProductByNav(nav) {
            return this.product.list.filter(item => {
                return item.is_show == 'Y'  && (item.nav_id == nav.id);
            });
            
        //   return this.filterProduct.filter(item => {
        //       return item.nav_id == nav.id;
        //   });
       },

       imgFormatter(src) {
           return imgFormatter(src);
       },
       
       getProductIcon(product) {
           let url = this.imgFormatter(product.icon);
           
          this.productPics.forEach(item => {
              if(item.id == product.id) {
                  url = item.img;
              }
          })
          
          return url;
       }
    }
}
</script>

